@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@100;200;300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: "Open Sans", 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 20px;
  background-color: #246d9a;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.main-circle{
   display: block;
   position: fixed;
   top: 50%;
   left: 50%;
   margin-top: -100px;
   margin-left: -100px;
}
h5{
   color: white;
}
.canvas{
   position: fixed;
   left: calc(50% - 95px);
   top: calc(50% - 95px);
}
.magnifier{
   background-color: transparent;
   padding: 5px;
   border: none;
   position: fixed;
   top: calc(50% + 20px);
   left: calc(50% - 45px); 
}
.visit{
   background-color: transparent;
   padding: 5px;
   border: none;
   position: fixed;
   top: calc(50% + 18px);
   left: calc(50% + 23px); 
}
.user-form{
   display: inline-flex;
   align-items: center;
   color: #fff; 
   border-radius: 5px;
   margin: 10px;
   padding: 30px 10px;
}
.text{
   padding: 0 10px;
}
select{
   background-color: transparent;
   padding: 0 1em 0 0;
   border-color: white;
   margin: 0;
   color: #fff;
   font-family: inherit;
   font-size: inherit;
   line-height: inherit;
}
.addButton{
   padding: 8px 15px;
   border-radius: 5px;
}
